<template>
  <div class="setting-container">
    <el-card class="box-card">
      <!--  -->
      <BreadLink menuName="" />
      <el-row>
        <el-col :span="12">
          <el-form
            ref="formRef"
            :model="user"
            label-width="100px"
            :rules="rules"
          >
            <el-form-item label=""> {{ user.id }} </el-form-item>
            <el-form-item label=""> {{ user.mobile }} </el-form-item>
            <el-form-item prop="name" label="">
              <el-input v-model="user.name"></el-input>
            </el-form-item>
            <el-form-item prop="intro" label="">
              <el-input v-model="user.intro"></el-input>
            </el-form-item>
            <el-form-item prop="email" label="">
              <el-input v-model="user.email"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button
                :loading="setBtnLoading"
                type="primary"
                @click="handleSubmit"
                ></el-button
              >
            </el-form-item>
          </el-form>
        </el-col>
        <el-col style="text-align: center" :offset="3" :span="5">
          <el-avatar
            shape="square"
            :size="200"
            fit="cover"
            :src="user.photo"
            @click="() => inputRef.click()"
          ></el-avatar>
          <p></p>
          <input ref="inputRef" hidden type="file" @change="handleChooseFile" />
        </el-col>
      </el-row>
    </el-card>

    <!--   -->
    <el-dialog
      title=""
      v-model="dialogAvatar"
      @opened="ondialogOpened"
      @closed="onDialogClosed"
    >
      <div class="perview-img-wrap">
        <img ref="imgRef" class="perview-img" :src="perviewImg" alt="" />
      </div>
      <template #footer>
        <el-button @click="() => (dialogAvatar = false)"> </el-button>
        <el-button
          type="primary"
          @click="handleUpdatePhoto"
          :loading="avaLoading"
          > </el-button
        >
      </template>
    </el-dialog>
  </div>
</template>


<script>
import BreadLink from "../../components/BreadLink.vue";
import { getUserProfile, updataUserAvatar, updataUser } from "../../api/user";
import { onMounted, reactive, ref } from "vue";
import { ElMessage } from "element-plus";
import "cropperjs/dist/cropper.css";
import Cropper from "cropperjs";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
export default {
  components: { BreadLink },
  setup() {
    const router = useRouter();
    const store = useStore();
    //
    const user = ref({});
    const inputRef = ref(null);
    //
    let dialogAvatar = ref(false);
    //
    let perviewImg = ref("");
    let imgRef = ref(null);
    //
    let cropper = null;
    //loading
    let avaLoading = ref(false);
    //loading
    let setBtnLoading = ref(false);
    //
    const rules = reactive({
      name: [
        { required: true, message: "", trigger: "change" },
        { min: 1, max: 7, message: " 1  7 ", trigger: "change" },
      ],
      intro: [{ required: true, message: "", trigger: "change" }],
      email: [
        { required: true, message: "", trigger: "change" },
        {
          pattern: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
          message: "",
          trigger: "change",
        },
      ],
    });
    //
    getUserProfile().then(res=>{
      user.value=res.data
    })
    //
    const handleSubmit = async () => {
      setBtnLoading.value = true;
      const res = await updataUser(user.value);
      if (res.message == "OK") {
        store.dispatch('getUser')
        setBtnLoading.value = false;
        ElMessage.success("~");
      }
    };
    //
    const handleChooseFile = () => {
      console.log(inputRef.value.files);
      //   src
      const blobData = window.URL.createObjectURL(inputRef.value.files[0]);
      console.log(blobData);
      perviewImg.value = blobData;
      //  
      // inputRef.value = ''
      dialogAvatar.value = true;
    };
    //
    const ondialogOpened = () => {
      //
      cropper = new Cropper(imgRef.value, {
        aspectRatio: 1, // 
        viewMode: 1, //   
        dragMode: "none", // 
        cropBoxResizable: false, //  
      });
    };
    //
    const onDialogClosed = () => {
      cropper.destroy();
    };
    //
    const handleUpdatePhoto = () => {
      avaLoading.value = true;
      console.log(cropper);
      cropper.getCroppedCanvas().toBlob((file) => {
        const fd = new FormData();
        fd.append("photo", file);
        updataUserAvatar(fd).then((res) => {
          store.dispatch('getUser')
          user.value.photo = window.URL.createObjectURL(file);
          dialogAvatar.value = false;
          avaLoading.value = false;
        });
      });
    };

    return {
      avaLoading,
      imgRef,
      setBtnLoading,
      perviewImg,
      dialogAvatar,
      inputRef,
      user,
      rules,
      handleSubmit,
      handleChooseFile,
      ondialogOpened,
      onDialogClosed,
      handleUpdatePhoto,
    };
  },
};
</script>

<style lang="scss" scoped>
.perview-img-wrap {
  .perview-img {
    display: block;

    /* This rule is very important, please don't ignore this */
    max-width: 100%;
    height: 300px;
  }
}
</style>